'use client';

import * as React from 'react';
import { useRouter, useSearchParams } from 'next/navigation';

type Talent = {
  id: string;
  department?: string | null;
  current_status?: string | null;
  pdi_plan_ready?: boolean | null;
  orchestrator_state?: string | null;
  start_date?: string | null;
  end_date?: string | null;
  date_updated: string;
  user_id: { email: string };
};

type TalentsResponse = {
  data: Talent[];
  meta?: { total_count?: number };
};

function useDebouncedValue<T>(value: T, delay = 400) {
  const [debounced, setDebounced] = React.useState(value);
  React.useEffect(() => {
    const id = setTimeout(() => setDebounced(value), delay);
    return () => clearTimeout(id);
  }, [value, delay]);
  return debounced;
}

export default function TalentsPage() {
  const router = useRouter();
  const sp = useSearchParams();

  // URL -> estado inicial
  const [q, setQ] = React.useState(sp.get('q') ?? '');
  const [page, setPage] = React.useState(Number(sp.get('page') ?? '1'));
  const [limit, setLimit] = React.useState(Number(sp.get('limit') ?? '10'));

  const [department, setDepartment] = React.useState(sp.get('department') ?? '');
  const [currentStatus, setCurrentStatus] = React.useState(sp.get('current_status') ?? '');
  const [orchestrator, setOrchestrator] = React.useState(sp.get('orchestrator_state') ?? '');
  const [pdi, setPdi] = React.useState(sp.get('pdi_plan_ready') ?? ''); // '', 'true', 'false'
  const [startDate, setStartDate] = React.useState(sp.get('start_date_gte') ?? '');
  const [endDate, setEndDate] = React.useState(sp.get('end_date_lte') ?? '');
  const [leaderId, setLeaderId] = React.useState(sp.get('leader_id') ?? '');
  const [targetRoleId, setTargetRoleId] = React.useState(sp.get('target_role_id') ?? '');

  // Debounce para busca e filtros
  const dq = useDebouncedValue(q);
  const dDepartment = useDebouncedValue(department);
  const dCurrentStatus = useDebouncedValue(currentStatus);
  const dOrchestrator = useDebouncedValue(orchestrator);
  const dPdi = useDebouncedValue(pdi);
  const dStartDate = useDebouncedValue(startDate);
  const dEndDate = useDebouncedValue(endDate);
  const dLeaderId = useDebouncedValue(leaderId);
  const dTargetRoleId = useDebouncedValue(targetRoleId);
  const dLimit = useDebouncedValue(limit);

  // Dados
  const [data, setData] = React.useState<TalentsResponse | null>(null);
  const [loading, setLoading] = React.useState(false);
  const [err, setErr] = React.useState<string | null>(null);

  // Sincroniza URL quando filtros/busca/limit mudarem (reseta page=1)
  React.useEffect(() => {
    const params = new URLSearchParams();
    params.set('page', String(1));
    params.set('limit', String(dLimit));
    if (dq)            params.set('q', dq);
    if (dDepartment)   params.set('department', dDepartment);
    if (dCurrentStatus)params.set('current_status', dCurrentStatus);
    if (dOrchestrator) params.set('orchestrator_state', dOrchestrator);
    if (dPdi)          params.set('pdi_plan_ready', dPdi);
    if (dStartDate)    params.set('start_date_gte', dStartDate);
    if (dEndDate)      params.set('end_date_lte', dEndDate);
    if (dLeaderId)     params.set('leader_id', dLeaderId);
    if (dTargetRoleId) params.set('target_role_id', dTargetRoleId);

    setPage(1);
    router.replace(`/talents?${params.toString()}`);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dq, dDepartment, dCurrentStatus, dOrchestrator, dPdi, dStartDate, dEndDate, dLeaderId, dTargetRoleId, dLimit]);

  // Recarrega quando page muda (mantém os demais params da URL)
  React.useEffect(() => {
    const params = new URLSearchParams(sp.toString());
    params.set('page', String(page));
    router.replace(`/talents?${params.toString()}`);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page]);

  // Busca dados sempre que searchParams mudarem
  React.useEffect(() => {
    let aborted = false;
    async function load() {
      setLoading(true);
      setErr(null);
      try {
        const res = await fetch(`/api/talents?${sp.toString()}`, { cache: 'no-store' });
        const json: TalentsResponse = await res.json();
        if (!aborted) setData(json);
      } catch (e: any) {
        if (!aborted) setErr(String(e));
      } finally {
        if (!aborted) setLoading(false);
      }
    }
    load();
    return () => { aborted = true; };
  }, [sp]);

  const total = data?.meta?.total_count ?? 0;
  const pageCount = Math.max(1, Math.ceil(total / Math.max(1, limit)));
  const from = total ? (page - 1) * limit + 1 : 0;
  const to = total ? Math.min(page * limit, total) : 0;

  return (
    <div className="p-6 max-w-7xl mx-auto space-y-6">
      <h1 className="text-2xl font-semibold">Talentos</h1>

      {/* Controles */}
      <div className="grid gap-3 md:grid-cols-4">
        <div className="md:col-span-2">
          <label className="block text-sm font-medium mb-1">Buscar por e-mail</label>
          <input
            className="w-full rounded-xl border px-3 py-2"
            placeholder="ex: talent0087@example.com"
            value={q} onChange={(e) => setQ(e.target.value)}
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Department</label>
          <input className="w-full rounded-xl border px-3 py-2"
                 placeholder="ex: Design"
                 value={department} onChange={(e)=>setDepartment(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Current Status</label>
          <input className="w-full rounded-xl border px-3 py-2"
                 placeholder="ex: ACTIVE, INACTIVE, ONBOARDING"
                 value={currentStatus} onChange={(e)=>setCurrentStatus(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Orchestrator State</label>
          <input className="w-full rounded-xl border px-3 py-2"
                 placeholder="ex: ACTIVE, PENDING"
                 value={orchestrator} onChange={(e)=>setOrchestrator(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">PDI Plan Ready</label>
          <select className="w-full rounded-xl border px-3 py-2"
                  value={pdi} onChange={(e)=>setPdi(e.target.value)}>
            <option value="">(qualquer)</option>
            <option value="true">true</option>
            <option value="false">false</option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Start Date ≥</label>
          <input type="date" className="w-full rounded-xl border px-3 py-2"
                 value={startDate} onChange={(e)=>setStartDate(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">End Date ≤</label>
          <input type="date" className="w-full rounded-xl border px-3 py-2"
                 value={endDate} onChange={(e)=>setEndDate(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Leader ID</label>
          <input className="w-full rounded-xl border px-3 py-2"
                 inputMode="numeric"
                 placeholder="ex: 5"
                 value={leaderId} onChange={(e)=>setLeaderId(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Target Role ID</label>
          <input className="w-full rounded-xl border px-3 py-2"
                 inputMode="numeric"
                 placeholder="ex: 2"
                 value={targetRoleId} onChange={(e)=>setTargetRoleId(e.target.value)} />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Por página</label>
          <select className="w-full rounded-xl border px-3 py-2"
                  value={limit} onChange={(e)=>setLimit(Number(e.target.value))}>
            {[5,10,20,50].map(n => <option key={n} value={n}>{n}</option>)}
          </select>
        </div>
      </div>

      {/* Estado / contagem */}
      <div className="text-sm text-gray-600">
        {loading && <span>Carregando…</span>}
        {!loading && err && <span className="text-red-600">Erro: {err}</span>}
        {!loading && !err && (
          <span>
            {total ? <>Mostrando <strong>{from}–{to}</strong> de <strong>{total}</strong></> : 'Nenhum resultado'}
          </span>
        )}
      </div>

      {/* Tabela */}
      <div className="overflow-x-auto rounded-2xl border">
        <table className="min-w-full text-sm">
          <thead className="bg-gray-50">
            <tr>
              <th className="text-left p-3">Email</th>
              <th className="text-left p-3">Department</th>
              <th className="text-left p-3">Status</th>
              <th className="text-left p-3">PDI</th>
              <th className="text-left p-3">Orchestrator</th>
              <th className="text-left p-3">Atualizado em</th>
            </tr>
          </thead>
          <tbody>
            {data?.data?.map((t) => (
              <tr key={t.id} className="border-t">
                <td className="p-3">{t.user_id?.email}</td>
                <td className="p-3">{t.department ?? '—'}</td>
                <td className="p-3">{t.current_status ?? '—'}</td>
                <td className="p-3">{String(t.pdi_plan_ready ?? '—')}</td>
                <td className="p-3">{t.orchestrator_state ?? '—'}</td>
                <td className="p-3">
                  {new Date(t.date_updated).toLocaleString('pt-BR')}
                </td>
              </tr>
            ))}
            {!loading && data?.data?.length === 0 && (
              <tr><td className="p-4 text-center text-gray-500" colSpan={6}>Sem resultados</td></tr>
            )}
          </tbody>
        </table>
      </div>

      {/* Paginação */}
      <div className="flex items-center justify-between gap-3">
        <button
          className="rounded-xl border px-3 py-2 disabled:opacity-50"
          onClick={() => setPage((p) => Math.max(1, p - 1))}
          disabled={page <= 1 || loading}
          aria-label="Página anterior"
        >
          « Anterior
        </button>
        <div className="text-sm">
          Página <strong>{page}</strong> de <strong>{pageCount}</strong>
        </div>
        <button
          className="rounded-xl border px-3 py-2 disabled:opacity-50"
          onClick={() => setPage((p) => Math.min(pageCount, p + 1))}
          disabled={page >= pageCount || loading}
          aria-label="Próxima página"
        >
          Próxima »
        </button>
      </div>
    </div>
  );
}
